@import url('https://fonts.googleapis.com/css2?family=Playwrite+HR+Lijeva:wght@100..400&family=Sacramento&display=swap');

* {
	box-sizing: border-box;

	button {
		background: inherit;
		border: none;
		box-shadow: none;
		border-radius: 0;
		padding: 0;
		overflow: visible;
		cursor: pointer;
	}
}

.playwrite {
	font-family: 'Playwrite HR Lijeva', cursive;
	font-optical-sizing: auto;
	font-weight: 400;
	font-style: normal;
}

.playwrite-300 {
	font-family: 'Playwrite HR Lijeva', cursive;
	font-optical-sizing: auto;
	font-weight: 300;
	font-style: normal;
}

.playwrite-200 {
	font-family: 'Playwrite HR Lijeva', cursive;
	font-optical-sizing: auto;
	font-weight: 200;
	font-style: normal;
}

.playwrite-100 {
	font-family: 'Playwrite HR Lijeva', cursive;
	font-optical-sizing: auto;
	font-weight: 100;
	font-style: normal;
}

.sacramento-regular {
	font-family: 'Sacramento', cursive;
	font-weight: 400;
	font-style: normal;
}

.fs-60 {
	font-size: 60px;
}

.fs-14 {
	font-size: 14px;
}

.fs-12 {
	font-size: 12px;
}

.container {
	width: 100vw;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;

	& > div:first-child {
		flex: 65%;
	}

	& > div:last-child {
		flex: 35%;
	}

	.city-name {
		position: fixed;
		bottom: 200px;
		left: 20%;
		font-size: 80px;
		z-index: 2;
	}

	.lang {
		color: black;
		position: fixed;
		z-index: 2;
		top: 10px;
		right: 10px;
		font-size: 20px;
		cursor: pointer;
	}

	.post-container {
		position: fixed;
		top: 0;
		left: 0;
		bottom: 0;
		right: 35%;
		overflow-y: scroll;
		overflow-x: visible;
		background-color: white;
		padding: 100px 150px 70px;
		margin: 0 20px 0 20px;
		z-index: 2;
		border-right: 1px solid #333;
		/* Hide scrollbar for Chrome, Safari and Opera */
		-ms-overflow-style: none; /* IE and Edge */
		scrollbar-width: none; /* Firefox */

		&::-webkit-scrollbar {
			display: none;
		}

		.ico-close {
			position: absolute;
			width: 80px;
			height: 80px;
			background-color: #333;
			color: #fff;
			top: 400px;
			right: 0px;
			font-size: 20px;
		}

		h1 {
			font-weight: 600;
			font-size: 20px;
			text-align: center;
			margin-bottom: 5px;
			position: fixed;
			top: 40px;
			font-family: 'Playwrite HR Lijeva', cursive;
			font-optical-sizing: auto;
			font-weight: 400;
			font-style: normal;
		}

		h2 {
			font-weight: 800;
			font-size: 25px;
			text-align: center;
			margin-bottom: 10px;
			position: fixed;
			top: 40px;
			left: 300px;
		}

		p {
			line-height: 1.7;
			margin-bottom: 20px;
			font-size: 18px;
			background-color: #fff;
			color: #333;
			display: inline-block;
			font-weight: 500;
		}

		em {
			font-style: italic;
			font-weight: 800;
		}

		img {
			width: 100%;
		}

		blockquote {
			background-color: aliceblue;
		}
	}

	.list-container {
		height: calc(100% - 100px);
		overflow-y: initial !important;

		div.padding {
			position: fixed;
			top: 0px;
			right: 0px;
			height: 300px;
			width: 33%;
			background: linear-gradient(white 70%, transparent);
		}

		div.title {
			position: fixed;
			top: 65px;
			right: 140px;

			p {
				text-shadow: 0px 0px 10px red;
			}

			.kr-title {
				text-align: right;
				padding-right: 20px;
			}
		}

		div.inner-container {
			display: flex;
			flex-direction: column;
			align-items: center;
			height: 100vh;
			padding: 500px 100px 500px 0;
			overflow-y: auto;
		}

		.item-container {
			display: flex;
			flex-direction: column;
			align-items: center;
			margin-bottom: 20px;
			cursor: pointer;
		}

		.item-title {
			margin-bottom: 3px;
		}

		button {
			width: 50px;
			height: 30px;
			display: inline-block;
			margin: 30px 0 20px;
			border: black 1px solid;
			border-radius: 100%;
			background-color: transparent;

			&:hover {
				background-color: black;
				span {
					color: white;
				}
			}

			&.active {
				background-color: black;
				span {
					color: white;
				}
			}

			span {
				color: black;
				width: 100%;
				display: block;
				text-align: center;
				margin: 6px auto;
			}
		}
	}
}
